import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	computed: {
		...mapState(['token', 'forcedLogin'])
	},
	data() {
		return {
			bankno: "",
			bankname: "",
			subbanknam: "",
		}
	},
	methods: {
		...mapMutations(['logout']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		async bindLogin() {
			const result = await this.$req.ajax({
				path: 'wxapi/member/maker_bank',
				title: '正在加载',
				data: {
					BankCardNo: this.bankno,
					BankName: this.bankname,
					SubBankName: this.subbanknam,
					token: this.token,
				}
			});
			if (result.data.code === 200) {
				this.login(result.data.data.token);
				uni.navigateTo({
					url: "/pages/main/main"
				});
			} else {
				this.$api.msg(result.data.message);
				this.logining = false;
			}


		}


	}
}
